.videoNavBar {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: hidden;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: -1;
}

.video {
    object-fit: cover;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
}

.NavBarLinks {
    text-decoration: none;
    font-size: 2.5rem;
    color: #f5deb3;
    margin-left: 30px;
    font-weight: 400;
}

.NavBarLinks:hover {
    font-weight: 600;
}

.selected {
    font-weight: 600;
    color: rgb(255, 196, 86)
}

.logoImage {
    width: 120px;
}

.navbar { 
    padding-left: 2%;
    padding-right: 2%;
}

.navbar-nav {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.logoWrapper {
    /* Control the size and position of the logo */
    transform: scale(1.7); /* Adjust the scale factor to make the image larger */
    transform-origin: left; /* Adjust the origin to keep it aligned with the left side */
}


@media only screen and (max-width: 991px) {
    .NavBarLinks {
        margin: 0;

    }

  }

@media only screen and (max-width: 767px) {
    .NavBarLinks {
        margin: 0;

    }

  }
